* {
  box-sizing: border-box;
  --primary-color: #fff;
  --secondary-color: #d50000;
  margin: 0;
  padding: 0;
  font-family: Poppins;
}

body {
  color: var(--primary-color);
  min-height: calc(100vh - 30px);
  background-color: #101111;
}

.hero {
  height: calc(100vh - 30px);
  color: var(--primary-color);
  border: 1px solid var(--primary-color);
  background-color: #101111;
  background-repeat: no-repeat;
  background-size: cover;
  margin: 15px;
  padding: 10px;
}

header {
  height: 75px;
}

.main-navigation {
  text-align: left;
  flex-direction: row;
  align-items: center;
  gap: 1em;
  list-style-type: none;
  display: flex;
}

.main-navigation > li > a, .main-navigation > li > a:visited {
  color: var(--primary-color);
  cursor: pointer;
  text-decoration: none;
}

.main-navigation > li > a:hover {
  color: var(--secondary-color);
}

.hero-main {
  margin-top: 60px;
  padding: 0 15px;
}

.left {
  text-align: right;
  width: fit-content;
}

.hero-title {
  width: 250px;
}

.hero-subtitle {
  margin-right: 10px;
  font-size: 12px;
  font-weight: 400;
}

.hero-content {
  flex-direction: column;
  gap: 1em;
  display: flex;
}

.hero-img {
  width: inherit;
}

@media (min-width: 880.001px) {
  .hero-content {
    flex-direction: row;
  }
}

@media (min-width: 1200px) {
  h1 {
    font-size: 5.5rem;
  }

  article {
    margin-top: 90px;
  }

  .hero-img {
    width: 700px;
    margin: 0 auto;
  }

  .left {
    margin-left: 50px;
  }

  .hero-title {
    width: 500px;
  }

  .hero-subtitle {
    margin-right: 10px;
    font-size: 18px;
    font-weight: 400;
  }
}

.work {
  flex-flow: wrap;
  justify-content: space-around;
  display: flex;
}

@media (min-width: 1000px) {
  .work {
    justify-content: flex-start;
  }
}

.card {
  color: var(--primary-color);
  width: 16rem;
  background: #252525;
  border-radius: 15px;
  flex-direction: column;
  gap: 10px;
  margin: .5rem;
  padding: 1rem;
  text-decoration: none;
  display: flex;
  position: relative;
}

.card:hover {
  cursor: pointer;
  transform: scale(1.01);
}

.card-title {
  font-size: 22px;
}

.card-text {
  --lh: 1.2rem;
  font-size: 16px;
  font-weight: 500;
  line-height: var(--lh);
  --max-lines: 5;
  max-height: calc(var(--lh)  * var(--max-lines) );
  position: relative;
  overflow: hidden;
}

.card-link {
  color: var(--secondary-color);
  margin-bottom: -10px;
  font-weight: 600;
  text-decoration: none;
  position: absolute;
  bottom: 15px;
}

.detail {
  color: var(--primary-color);
  flex-direction: column;
  gap: 20px;
  padding: 10px;
  display: flex;
}

.volume {
  flex-direction: column;
  align-items: center;
  gap: 20px;
  display: flex;
}

.volume-img {
  width: fit-content;
}

@media (min-width: 650.001px) {
  .volume {
    flex-direction: row;
    align-items: flex-start;
  }

  .volume-img {
    height: fit-content;
  }
}

.volume-body {
  flex-direction: column;
  gap: 10px;
  margin-bottom: 15px;
  display: flex;
}

.volume-genres {
  list-style: circle;
}

.volume-genres > li {
  margin-left: 20px;
}

.author-main {
  margin-bottom: 20px;
  padding: 0 15px;
}

.author-title {
  margin-bottom: 20px;
}

.author-content {
  flex-direction: column;
  gap: 10px;
  margin-bottom: 15px;
  display: flex;
}

.author-img {
  max-width: 350px;
}

@media (min-width: 1350px) {
  .author-content {
    flex-direction: row;
    align-items: flex-start;
  }

  .author-img {
    height: fit-content;
  }
}

.figure {
  flex-direction: column;
  display: flex;
}

.sources-section {
  padding: 10px 45px;
}

.link {
  color: var(--primary-color);
  cursor: pointer;
  text-decoration: none;
}

.link:hover {
  color: var(--secondary-color) !important;
}

.link:visited {
  color: var(--primary-color);
  cursor: pointer;
  text-decoration: none;
}

