
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins';
  --primary-color: #ffff;
  --secondary-color:#d50000;
}

body {
    color: var(--primary-color);
    background-color: #101111;
    min-height: calc(100vh - 30px);
}
.hero{
  height: calc(100vh - 30px);
  color: var(--primary-color);
  background-color: #101111;
  border: 1px solid var(--primary-color);
  margin:15px;
  padding:10px;
  background-repeat: no-repeat;
  background-size: cover;
}
header {
    height: 75px;
}
.main-navigation{
    list-style-type: none;
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 1em;
    text-align: left;
}

.main-navigation > li > a, .main-navigation > li > a:visited  {
    text-decoration: none;
    color: var(--primary-color);
    cursor:pointer;
}

.main-navigation > li > a:hover  {
    color: var(--secondary-color);

}
.hero-main {
  margin-top: 60px;
  padding:0 15px;
}
.left {
  text-align:right;
  width: fit-content;
}
.hero-title{
  width: 250px;
}
.hero-subtitle {
  margin-right: 10px;
  font-size: 12px;
  font-weight: 400;
}
.hero-content{
    display: flex;
    flex-direction: column;
    gap:1em;
}
.hero-img{
  width: inherit;
}
@media (width > 880px) {
  .hero-content{
    flex-direction: row;  
  } 

}

@media (width > 1200px) {
  h1 {
    font-size: 5.5rem;
  }
  article {
    margin-top: 90px;
  }
  .hero-img{
    margin: 0 auto;
    width: 700px;
  }
  .left {
    margin-left: 50px;
  }
  .hero-title{
    width: 500px;
  }
  .hero-subtitle {
    margin-right: 10px;
    font-size: 18px;
    font-weight: 400;
  }
}
/******************* WORK *******************/
.work {
  display: flex;
  justify-content: space-around;
  flex-flow: row wrap;
}
@media (width > 1000px) {
  .work {
    justify-content: flex-start;
  }
}

.card {
  text-decoration: none;
  margin: .5rem;
  padding: 1rem;
  color: var(--primary-color);
  background: rgb(37, 37, 37);
  display:flex;
  flex-direction: column;
  width: 16rem;
  border-radius: 15;
  gap: 10px;
  position: relative;
}
.card:hover{
  transform: scale(1.01);
  cursor: pointer;
}
.card-title{
  font-size: 22px;
}
.card-text{
  font-size: 16px;
  font-weight: 500;
  --lh: 1.2rem;
  line-height: var(--lh);
  --max-lines: 5;
  max-height: calc(var(--lh) * var(--max-lines));
  overflow: hidden;
  position: relative;
}

.card-link{
  margin-bottom: -10px;
  position: absolute;
  color: var(--secondary-color);
  text-decoration: none;
  font-weight: 600;
  bottom: 15px;
}

/************* DETAIL ******************/
.detail{
  display: flex;
  flex-direction: column;
  color: var(--primary-color);
  gap: 20px;
  padding: 10px;
}
.volume {
  display:flex;
  flex-direction: column;
  gap: 20px;
  align-items: center;
}
.volume-img{
  width: fit-content;
}
@media (width > 650px) {
  .volume {
    flex-direction: row;
    align-items: flex-start;
  }
  .volume-img{
    height: fit-content;
  }
}
.volume-body{
  display:flex;
  flex-direction: column;
  gap: 10px;
  margin-bottom: 15px;
}
.volume-genres{
  list-style: circle;
}
.volume-genres>li{
  margin-left: 20px;
}
/********* AUTHOR **********/
.author-main{
  padding: 0 15px;
  margin-bottom:20px;
}
.author-title{
  margin-bottom: 20px;
}
.author-content{
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-bottom: 15px;
}
.author-img{
max-width: 350px;
}
@media (width > 1350px) {
  .author-content{ 
    flex-direction: row;
    align-items: flex-start;
  }
  .author-img{
    height: fit-content;
  }
}
.figure {
  display: flex;
  flex-direction: column;
}
/********* SOURCES **********/
.sources-section{
  padding: 10px 45px;
}
.link{
text-decoration: none;
color: var(--primary-color);
  cursor:pointer;
}
.link:hover{
  color: var(--secondary-color) !important;
}
.link:visited{
  text-decoration: none;
  color: var(--primary-color);
  cursor:pointer;
}
